import Image from "next/image";
import profileImage from "@/src/assets/images/avatar-jessica.jpeg";

export default function Home() {
  return (
    <div>
      <div
        className="mx-auto max-w-sm items-center gap-x-4 rounded-xl bg-white p-6 shadow-lg 
      outline outline-black/5 dark:bg-slate-800 dark:shadow-none dark:-outline-offset-1 dark:outline-white/10">
  
        <div>
          <Image src="profileImage" width={100} height={100} alt="Imagen de perfil" />
        </div>      

        <p>Jessica Randall</p>
        <p>London, United Kingdom</p>
        <p>"Front-end developer and avid reader."</p>
        <p>GitHub</p>
        <p>Frontend Mentor</p>
        <p>LinkedIn</p>
        <p>Twitter</p>
        <p>Instagram</p>
      </div>

      <div className="attribution">
        Challenge by{" "}
        <a href="https://www.frontendmentor.io?ref=challenge" target="_blank">
          Frontend Mentor
        </a>
        . Coded by <a href="#">Danielly Cardoso</a>.
      </div>
    </div>
  );
};



